import React, {useState, useEffect, Fragment} from 'react';
import {
    Header,
    Placeholder,
    Panel,
    PanelHeader,
    PanelHeaderBack,
    Button,
    MiniInfoCell,
    Div,
    Footer, Counter, Group, Cell, Select
} from "@vkontakte/vkui";
import './Home.css'
import bridge from '@vkontakte/vk-bridge';
import Icon56ArticleOutline from '@vkontakte/icons/dist/56/article_outline';
import Delivery from './components/Delivery'
import CartListItems from "./components/CartListItems";

const Cart = ({ id, go, goBack,  typeDelivery, fetchedUser, activeCity, onDecrementPosition, onIncrementPosition, order, clearCart, onDeletePosition, activePVZ, setActivePVZ }) => {
    const [deliv, setDeliv] = useState(null);

    useEffect(() => {
        console.log('Изменились рассчетные данные')
        if (activeCity !== null && activePVZ !== null) getRange(activeCity.id, typeDelivery)
    }, [activeCity, typeDelivery, order, activePVZ]);

    const getRange = () => {
        fetch('https://sahpossum.herokuapp.com/items/getRange', {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json',
                // 'Content-Type': 'application/x-www-form-urlencoded',
            },
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // no-referrer, *client
            body: JSON.stringify({
                "cityCode": activeCity.id,
                "weight": String(order.meta.weight),
                "count": order.meta.count,
                "typeDelivery": activePVZ.id === 123 ? "137" : "136"
            }), // тип данны
        })
            .then(res=>res.json())
            .then(res=>{
                setDeliv(res)
            })
            .catch(e=>{
                console.log(e)
            })
    }

    const pay = (fetchedUser) => {
        if (fetchedUser.id){
            bridge.send("VKWebAppOpenPayForm", {"app_id": 7600142, "action": "pay-to-user", "params": {
                    "amount": order.meta.sum +Math.round(Number(deliv.price)),
                    "user_id": 199500866,
                    "description": `Оплата товаров на сумму ${order.meta.sum +Math.round(Number(deliv.price))} в магазине (в рамках тестирования сумма 1 рубль)`
                },
            });
        }
    };

    if (order.meta && order.meta.count === 0){
        return (
            <Panel id={id}>
                <PanelHeader left={<PanelHeaderBack onClick={()=>goBack} />}/>
                <Placeholder
                    stretched
                    icon={<Icon56ArticleOutline />}
                    header={'Ваша корзина пуста'}
                    action={<Button onClick={()=>goBack} mode={'outline'} size="l">Продолжить покупки</Button>}
                />
                <Footer/>
            </Panel>
        )
    } else {
        return (
            <Panel id={id}>
                <PanelHeader left={<PanelHeaderBack onClick={()=>go('home')} />}/>
                <CartListItems
                    order={order}
                    onDeletePosition={onDeletePosition}
                    onDecrementPosition={onDecrementPosition}
                    onIncrementPosition={onIncrementPosition}
                    clearCart={clearCart}
                />
                <Fragment>
                    <Div>
                        <MiniInfoCell after={<Counter>{order.meta ? order.meta.count : 0}</Counter>}>Товаров (штуки)</MiniInfoCell>
                        <MiniInfoCell after={<Counter>{order.meta ? order.meta.sum : 0}</Counter>}>Сумма (рубли)</MiniInfoCell>
                    </Div>
                        <Group header={<Header mode="secondary">Доставка</Header>}>
                            <Cell
                                expandable
                                onClick={()=>go('GetCityList')}
                                indicator={activeCity !== null ? activeCity.name : 'Не выбрано' }
                            >Город</Cell>
                            {
                                activeCity !== null &&
                                <Cell
                                    expandable
                                    indicator={activePVZ !== null ? activePVZ.name : 'Не выбрано' }
                                    onClick={()=>go('GetPvzList')}
                                >Пункт выдачи</Cell>
                            }
                            {
                                deliv !== null &&
                                <Div>
                                    <Delivery deliv={deliv} sum={order.meta.sum} />
                                    {
                                        !deliv.error &&
                                        <Button after={<Counter>{order.meta.sum +Math.round(Number(deliv.price))}</Counter>} mode={'outline'} size={'xl'}  onClick={()=>pay(fetchedUser)}>Оплатить</Button>
                                    }
                                </Div>
                            }
                        </Group>
                </Fragment>
                <Footer/>
            </Panel>
        )
    }
};

export default Cart
